<template>
  <div class="job-listing">
    <div class="card-container">
      <div 
        v-for="job in paginatedJobs" 
        :key="job.id" 
        class="job-card" 
        :style="{ backgroundColor: getRandomColor() }"
      >
        <h3>{{ job.type }}</h3>
        <p><strong>描述:</strong> {{ job.content }}</p>
        <p><strong>作者:</strong> {{ job.author }}</p>
        <p><strong>电话:</strong> {{ job.phone }}</p>
        <p><strong>微信:</strong> {{ job.wechat }}</p>
        <p><strong>发布日期:</strong> {{ job.date }}</p>
      </div>
    </div>
    <el-pagination
      @current-change="handleCurrentChange"
      @size-change="handlePageSizeChange"
      :current-page="currentPage"
      :page-sizes="[5, 10, 20]"
      :page-size="pageSize"
      :total="jobs.length"
      layout="total, sizes, prev, pager, next, jumper"
      class="pagination"
    />
  </div>
</template>

<script setup>
import axios from 'axios';
import { computed, onMounted, ref } from 'vue';

const jobs = ref([]);
const currentPage = ref(1);
const pageSize = ref(10); // 每页展示10条数据

// 计算当前页显示的数据
const paginatedJobs = computed(() => {
  const start = (currentPage.value - 1) * pageSize.value;
  return jobs.value.slice(start, start + pageSize.value);
});

const handleCurrentChange = (page) => {
  currentPage.value = page;
};

const handlePageSizeChange = (size) => {
  pageSize.value = size;
  currentPage.value = 1; // 重置为第一页
};

const fetchJobs = async () => {
  try {
    const response = await axios.get('http://localhost:8090/volunteer', {
      withCredentials: true,
      timeout: 10000
    });
    if (response.data) {
      console.log('获取到的数据:', response.data);
      jobs.value = response.data;
    } else {
      console.error('响应体为空');
    }
  } catch (error) {
    console.error('获取职位数据失败:', error.message);
  }
};

// 随机生成颜色
const getRandomColor = () => {
  const letters = '0123456789ABCDEF';
  let color = '#';
  for (let i = 0; i < 6; i++) {
    color += letters[Math.floor(Math.random() * 16)];
  }
  return color;
};

// 页面加载时获取职位数据
onMounted(fetchJobs);
</script>

<style scoped>
html, body {
  height: 100%;
  margin: 0;
}

.job-listing {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh; /* 使容器高度为视口高度 */
  padding: 20px;
  font-family: Arial, sans-serif;
  background: linear-gradient(135deg, #74ebd5, #acb6e5); /* 炫酷背景 */
  position: relative; /* 为了定位分页 */
}

.card-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
  width: 100%; /* 使卡片容器宽度为100% */
  max-width: 800px; /* 限制最大宽度 */
}

.job-card {
  border-radius: 10px;
  padding: 20px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s, box-shadow 0.3s;
  color: #fff; /* 字体颜色为白色，适合多种背景 */
  position: relative; /* 为了后面的伪元素 */
  overflow: hidden; /* 隐藏溢出内容 */
}

.job-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3);
}

.job-card h3 {
  margin: 0 0 10px;
  font-size: 1.5em; /* 增加标题字体大小 */
  text-align: center; /* 居中标题 */
}

.job-card p {
  margin: 5px 0;
}

/* 添加伪元素以增强卡片效果 */
.job-card::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(255, 255, 255, 0.1); /* 半透明白色覆盖 */
  transition: opacity 0.3s;
  opacity: 0; /* 初始透明 */
}

.job-card:hover::before {
  opacity: 1; /* 悬停时显示 */
}

/* 分页样式 */
.pagination {
  position: absolute; /* 固定位置 */
  bottom: 20px; /* 距离底部20px */
  right: 20px; /* 距离右侧20px */
}
</style>

